import React, { Component } from "react";

class Purpose extends Component {
  render() {
    return (
      <div class="accordion" id="accordionExample">
        <div class="card">
          <div class="card-header" id="headingOne">
            <h2 class="mb-0">
              <button
                class="btn "
                type="button"
                data-toggle="collapse"
                data-target="#collapseOne"
                aria-expanded="true"
                aria-controls="collapseOne"
              >
                Assumptions of the model
              </button>
            </h2>
          </div>

          <div
            id="collapseOne"
            class="collapse"
            aria-labelledby="headingOne"
            data-parent="#accordionExample"
          >
            <div class="card-body">
              <p class="font-weight-bold">
                We are modeling the change in concentration of reactant A (C
                <sub>a</sub>) in this CSTR under the following assumptions:
              </p>
              <ul class="list-group list-group-flush">
                <a
                  href="https://en.wikipedia.org/wiki/Continuous_stirred-tank_reactor"
                  class="list-group-item list-group-item-action"
                  target="parent"
                  rel="noopener"
                >
                  Ideal CSTR - perfectly mixed (no spatial variations).
                </a>

                <a
                  href="https://en.wikipedia.org/wiki/Isothermal_process"
                  class="list-group-item list-group-item-action"
                  target="parent"
                  rel="noopener"
                >
                  Isothermal reactor operation.
                </a>

                <a
                  href="https://en.wikipedia.org/wiki/Reaction_rate"
                  class="list-group-item list-group-item-action"
                  target="parent"
                  rel="noopener"
                >
                  The reaction proceeds at a Zeroth-Order or First-order rate with respect to A (this is also the overall reaction order).
                  r<sub>a</sub>=-k[C<sub>a</sub>]<sup>(0 or 1)</sup>
                </a>

                <a
                  href="#"
                  class="list-group-item list-group-item-action disabled"
                
                >
                  Fixed volume of the reactor (not a function of time).
                </a>
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Purpose;
