import React, { Component } from "react";

class NavBar extends Component {
  render() {
    return (
      <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
        <a
          class="navbar-brand"
          href="https://www.linkedin.com/in/sary-sarkis/"
          target="parent"
          rel="noopener"
        >
          About Author
        </a>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="true"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarNav">
          <ul class="navbar-nav">
            <li class="nav-item active">
              <a
                class="nav-link"
                href="http://encyclopedia.che.engin.umich.edu/Pages/Reactors/CSTR/CSTR.html"
                target="parent"
                rel="noopener"
              >
                What is a CSTR? <span class="sr-only">(current)</span>
              </a>
            </li>
          </ul>

          <ul class="navbar-nav">
            <li class="nav-item active">
              <a
                class="nav-link"
                href="mailto:sarkisopolis@gmail.com?subject=This email originates from the CSTR Website!"
              >
                Email me! <span class="sr-only">(current)</span>
              </a>
            </li>
          </ul>
        </div>
      </nav>
    );
  }
}

export default NavBar;
