import React, { Component } from "react";
import CSTR from "./CSTR.png";
import SelectOrder from "./SelectOrder";
import Inputs from "./Inputs";
import Fetch from "./Fetch";

class Graphic extends Component {
  state = {
    order: 0,
    concentration: 0,
    volume: 0,
    flow: 0,
    k: 0,
  };



  handleZero = () => {
    this.setState({ order: 0 });
  };

  handleFirst = () => {
    this.setState({ order: 1 });
  };

  handleChange1 = (event) => {
    event.target.value <= 0.00000001
      ? console.log("You cannot have a negative or 0 concentration")
      : this.setState({ concentration: event.target.value });
  };

  handleChange2 = (event) => {
    event.target.value <= 0.00000001
      ? console.log("you have no flow or backflow apparently")
      : this.setState({ flow: event.target.value });
  };

  handleChange3 = (event) => {
    event.target.value <= 0.00000001
      ? console.log("Your reactor is imaginary")
      : this.setState({ volume: event.target.value });
  };

  handleChange4 = (event) => {
    event.target.value <= 0.00000001
      ? console.log("Need a reaction")
      : this.setState({ k: event.target.value });
  };

  handleSubmit = (event) => {
    alert("hi");
    event.preventDefault();
  };

  render() {
    return (
      <div >
        <SelectOrder
          order={this.state.order}
          onZero={this.handleZero}
          onFirst={this.handleFirst}
        />

        <div class="container-fluid row justify-content-md-center"  >
          <div >
            <div class="col-lg-9 ">
              <Inputs
                variables={this.state}
                change2={this.handleChange2}
                change1={this.handleChange1}
                change3={this.handleChange3}
                change4={this.handleChange4}
              />


            </div>
            <div class="col-lg-8 " clearfix visible-xs>
            <img 
                src={CSTR}
                class="img-fluid rounded"
                alt="CSTR"
              ></img>
            </div>
          </div>
          <div class="col-lg-auto ">
            <Fetch variables={this.state} />
          </div>
        </div>
      </div>
    );
  }
}

export default Graphic;
