import React, { Component } from "react";

class Inputs extends Component {
  render() {
    return (
      <form onSubmit={this.props.submit}>
        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <span class="input-group-text">
              C <sub>a0</sub> {"\u00A0"} (mol/L)
            </span>
          </div>
          <input
            onChange={this.props.change1}
            type="number"
            class="form-control"
            min="0"
            placeholder="Reactant Concentration"
            required="required"
            step="0.001"
          />
        </div>

        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <span class="input-group-text">ν (L/s)</span>
          </div>
          <input
            onChange={this.props.change2}
            type="number"
            class="form-control"
            min="0"
            placeholder="Flow Rate"
            required="required"
            step="0.001"
          />
        </div>

        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <span class="input-group-text">V (L)</span>
          </div>
          <input
            onChange={this.props.change3}
            type="number"
            class="form-control"
            min="0"
            placeholder="Reactor Volume"
            required="required"
            step="0.001"
          />
        </div>
        
        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <span class="input-group-text">k (mol/s or 1/s)</span>
          </div>
          <input
            onChange={this.props.change4}
            type="number"
            class="form-control"
            min="0"
            placeholder="Rate Constant"
            required="required"
            step="0.001"
          />
        </div>
      </form>
    );
  }
}

export default Inputs;
